import $ from 'jquery';

class Accordions {
	constructor(){
		// Get elements
		this.getElements();

		// Create accordions
		this.createAccordions();
	}

	getElements(){
		this.$elements = {
			accordions: $( '.uo-admin-accordion' )
		}
	}

	createAccordions(){
		// Iterate accordions
		$.each( this.$elements.accordions, ( index, accordion ) => {
			// Create new accordion instance
			new Accordion( $( accordion ) );
		});
	}
}

class Accordion {
	constructor( $accordion ){
		// Set configuration
		this.setConfiguration();

		// Get elements
		this.getElements( $accordion );

		// Check if we have to open one on first load
		this.maybeOpenOneOnFirstLoad();

		// Listen clicks to the headers
		this.listenClicksToHeaders();
	}

	setConfiguration(){
		this.config = {
			slideSpeed:     500,
			onlyOneOpen:    true,
			allowAllClosed: true
		}
	}

	getElements( $accordion ){
		this.$elements = {
			container: $accordion,
			items:     $accordion.find( '.uo-admin-accordion-item' ),
			headers:   $accordion.find( '.uo-admin-accordion-item__header' ),
			contents:  $accordion.find( '.uo-admin-accordion-item__content' )
		}
	}

	maybeOpenOneOnFirstLoad(){
		// Check if all the items are closed and allowAllClosed is false
		if ( this.$elements.items.filter( '.uo-admin-accordion-item--open' ).length == 0  && ! this.config.allowAllClosed ){
			// Open the first one
			this.openItem( this.$elements.items.first() );
		}
	}

	listenClicksToHeaders(){
		// Listen clicks to the headers
		this.$elements.headers.on( 'click', ( event ) => {
			// Get clicked header
			const $header = $( event.currentTarget );

			// Get the whole item
			const $item = $header.closest( '.uo-admin-accordion-item' );

			// Check if it's open or closed
			if ( $item.hasClass( 'uo-admin-accordion-item--open' ) ){
				// So, the user is trying to close it
				// Get the number of opened items
				const openedItems = this.$elements.items.filter( '.uo-admin-accordion-item--open' ).length;

				// Check if there is more than one items opened
				if ( openedItems == 1 ){
					// Check if we can have all the items closed
					if ( this.config.allowAllClosed ){
						// Close it
						this.closeItem( $item );
					} else {
						// Do nothing, don't close it
					}
				} else {
					// Close it
					this.closeItem( $item );
				}
			}
			else {
				// The user is trying to open it
				// Check if we have to close all the other items first
				if ( this.config.onlyOneOpen ){
					// Close all the items, except this one
					this.closeAllItems( $item );
				}

				// Open the item
				this.openItem( $item );
			}
		});
	}

	openItem( $item ){
		// Get content
		const $content = $item.find( '.uo-admin-accordion-item__content' );

		// Show the content
		$content.slideDown( this.config.slideSpeed );

		// Add open class
		$item.addClass( 'uo-admin-accordion-item--open' );
	}

	closeItem( $item ){
		// Get content
		const $content = $item.find( '.uo-admin-accordion-item__content' );

		// Hide the content
		$content.slideUp( this.config.slideSpeed );

		// Remove open class
		$item.removeClass( 'uo-admin-accordion-item--open' );
	}

	closeAllItems(){
		// Close all items
		const $visibleItems = this.$elements.items.filter( '.uo-admin-accordion-item--open' );

		// Remove open classes
		$visibleItems.removeClass( 'uo-admin-accordion-item--open' );

		// Hide contents
		$visibleItems.find( '.uo-admin-accordion-item__content' ).slideUp( this.config.slideSpeed );
	}
}

export default Accordions;