(function ($) {
	$(function () {
		//var url = ULGM_USER_AUTOCOMPLETE_AJAX.url + "?action=search_user&name=" + $('#ulgm_group_leader_email').val();
		if ( $("#ulgm_group_leader_email").length > 0 ){
			var url = ULGM_USER_AUTOCOMPLETE_AJAX.url + "?action=search_user";
			$("#ulgm_group_leader_email").autocomplete({
				source: url,
				delay: 500,
				minLength: 3
			});
		}
	});

})(jQuery);

